.footer{
  background-color: #171717;
  padding: 40px 0px;

  &__nav{
   display: flex;
   justify-content: center;
   margin-bottom: 40px;
}
&__item{
  &:not(:last-child){
    margin-right: 30px;
  }
}
&__text{
  margin: 0;
  text-align: center;
  color: #fff;
  font-size: 40px;

  span{
    color: var(--red-color);
  }
}
}
