.feedback{
  background-color: var(--gray-color);
  min-height: 700px;

  &__border{
    margin: 0 auto;
    border: none;
  }
  &__swiper{
   position: relative;
   left: 10px;
    border-radius: 20px;
    border: 2px solid var(--red-color);
    padding: 10px;
    width: 350px;
    height: 550px;
  }
  &__img{
    width: 340px;
    height: 500px;
  }
  &__title{
    margin: 0;
    padding-bottom: 40px;
    text-align: center;
  }
  &__text{
    font-size: 14px;
    color: var(--red-color);

  }
}




