.contacts{
  background-color: var(--gray-color);

  &__title{
    margin: 0;
    font-size: 40px;
    font-weight: 600;
    text-align: center;
    padding: 40px 0px;
  }
  &__inner{
    margin: 0 auto;
    max-width: 1100px;
    display: flex;
    gap: 40px;
   padding-bottom: 80px;
  }
  &__address{
    margin: 0;
    font-size: 20px;
    margin-bottom: 20px;

    span{
      font-weight: 700;
    }
  }
  &__phone{
    display: block;
    font-size: 20px;
    margin-bottom: 20px;
    color: #000;

    span{
      font-weight: 700;
    }
  }
  &__contacts{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  &__sotial-list{
    display: flex;
    gap: 10px;
  }
}
