.price{
  background-color: #171717;

  &__title{
    margin: 0;
    padding: 80px 0px 20px 0px;
    font-size: 40px;
    font-weight: 600;
    text-align: center;
    color: var(--red-color);
  }
  &__subtitle{
    margin: 0;
    padding-bottom: 20px;
    text-align: center;
    font-size: 20px;
    color: #fff;
  }
  &__descr{
    margin: 0 auto;
    max-width: 600px;
    padding-bottom: 20px;
    line-height: 30px;
    font-size: 20px;
    color: #fff;
  }
  &__text-bottom{
    margin: 0 auto;
    text-align: center;
    max-width: 600px;
    padding-bottom: 20px;
    line-height: 30px;
    font-size: 20px;
    color: #fff;
    padding-bottom: 80px;
  }
  &__vidio{
    display: block;
    margin: 0 auto;
    padding-bottom: 80px;
  }
}
