.comfort{
  background-color: var(--gray-color);

  &__title{
    margin: 0;
    text-align: center;
    padding: 80px 0px;
    font-family: Inter;
    font-size: 40px;
    font-weight: 600;
  }
  &__text-box{
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__list{
    padding-bottom: 80px;
  }
  &__text{
    position: relative;
    margin: 0;
    padding: 0px 10px;
    font-family: Inter;
    text-align: center;
    font-size: 25px;
    font-weight: 700px;
    line-height: 35px;
  }
  &__descr{
    font-family: Inter;
    text-align: center;
    font-size: 20px;
    font-weight: 400px;
  }
}
