.discount{

  &__title{
    margin: 0 auto;
    max-width: 1000px;
    padding: 100px 0px;
    font-family: Inter;
    font-size: 60px;
    font-weight: 600;
    text-align: center;

    span{
      color: var(--red-color);
      text-decoration: underline;
    }
  }
  &__form{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 40px;
  }
  &__input{
    outline: none;
    padding-left: 30px;
    border-radius: 30px;
    width: 250px;
    height: 60px;
    border: 1px solid #171717;
    margin-right: 20px;

    &-send{
      padding-left: 0;
      font-family: Inter;
      font-size: 20px;
      font-weight: 400;
      color: var(--light-color);
      background-color: var(--red-color);

      &:hover{
        transition: all 1s;
        background-color: #171717;
      }
    }
    &::placeholder{
      color: var(--light-grey-color);
    }
  }
  &__descr{
    margin: 0 auto;
    max-width: 1000px;
    text-align: center;
    font-family: Inter;
    font-size: 20px;
    font-weight: 400;
    color: var(--light-grey-color);
    margin-bottom: 40px;
  }
}
