.hero{
  position: relative;
  background-color: #171717;
  color: #fff;


  &__title{
    margin: 0;
    padding-top: 150px;
    font-family: Inter;
    font-size: 60px;
    font-weight: 400;
    text-align: center;

    span{
     color: var(--red-color);
    }

  }

  &__subtitle{
    display: block;
    text-align: center;
    color: var(--red-color);
    margin: 0 auto;
    max-width: 700px;
    font-family: Inter;
    font-size: 30px;
    font-weight: 400;
    padding-bottom: 20px;


    span{
      color: #fff;
    }

  }
  &__descr{
    margin: 0 auto;
    max-width: 1100px;
    text-align: center;
    font-family: Inter;
    font-size: 30px;
    line-height: 40px;
    font-weight: 400;
    padding-bottom: 40px;

  }
  &__btn{
    padding-bottom: 100px;
  }
  &__link{
    display: block;
    margin: 0 auto;
    border-radius: 30px;
    border: 1px solid #fff;
    padding: 20px 25px;
    font-family: Inter;
    font-size: 20px;
    font-weight: 400;
    width: 200px;
    height: 60px;
    color: #fff;
    background-color: #D0016E;

    &:hover{
      transition: all 1s;
      background-color: #171717;
    }
  }

}
.social{
position: absolute;
top: 100px;
left: 50px;
  &__item{
    &:not(:last-child){
      margin-bottom: 10px;
    }
  }
}
