.rules{
  background-color: #171717;
  color: #fff;

  &__inner{
    padding: 80px 0px;
    margin: 0 auto;
    max-width: 1000px;
  }
  &__descr{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 20px;
    margin: 0;
    padding-left: 30px;
    font-size: 20px;
    padding-bottom: 20px;

    &::before{
      // position: absolute;
      content: url('../img/svg/check.svg');
      left: 0;
      top: 10px;
    }
  }

}
