.accordion__list{
  margin: 0 auto;
  max-width: 1000px;
  padding-bottom: 80px;
}
.accordion{
border-bottom: 1px solid  #fff;


&__control {
  text-align: left;
  width: 100%;
  background-color: transparent;
  cursor: pointer;
  font-size: 20px;
  line-height: 20px;
  color: #242424;

  }

&__content {
  font-weight: 400;
  font-size: 20px;
  color: var(--red-color);
  max-height: 0;
  opacity: 0;
  overflow: hidden;
  padding-right: 70px;
  will-change: max-height;
  transition: all 0.3s ease-out;
  box-sizing: content-box;
}
&__title {
  font-size: 35px;
  font-weight: 700;
  color: var(--red-color);
  text-decoration: underline;
}

&__icon {
  display: inline-block;
  width: 25px;
  height: 25px;
  background-image: url("../img/cloze.svg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  transition: transform 0.3s ease-out;
}
&:not(:first-child){
  margin-top: -1px;
}
&__box{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
}

.open .accordion__icon {
  transform: rotate(90deg);
}

.open .accordion__content {
  opacity: 1;
  padding-bottom: 18px;}
