.photo{
  background-color: #171717;
  position: relative;

  &__list{
    margin: 0 auto;
    padding: 80px 0px;
    max-width: 980px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 40px;
  }

  &__img{
    width: 300px;
    height: 300px;
  }
  &__icon{
    display: block;
    fill: aqua;
  }
}
