.swiper{
  position: absolute;
  top: 20px;
  right: 30px;
  width: 240px;
  height: 360px;
}

.swiper-slide{
  border-radius: 20px;
  width: 240px;
  height: 320px;
}

.swiper-pagination-bullet {
  background: var(--red-color);
}
.swiper-scrollbar-drag{
  background: #141024;
}
.swiper-navigation{
  color: var(--red-color);
}
.swiper-button-next{
  fill: var(--red-color);
}
.swiper__img{
  border-radius: 20px;
}
